import React, { useState } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import styled from "styled-components";
const View = styled.div`
    width: 90%;
    background: navy;
    border: 1px solid black;
    padding: 10px 0;
    margin: 10px;
    color: white;
`;
const DroppableArea = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: start; // 드래그 가능한 항목들이 위에서 시작하도록 조정
    background-color: lightgrey; // 배경색 추가로 영역 시각화
    width: 100%; // 또는 필요에 따라
    margin: auto; // 중앙 정렬을 위해
`;

// 초기 항목 목록 정의
const initialItems = [
    { id: "item-1", content: "항목 1" },
    { id: "item-2", content: "항목 2" },
    { id: "item-3", content: "항목 3" },
    { id: "item-4", content: "항목 4" },
    { id: "item-5", content: "항목 5" },
    { id: "item-6", content: "항목 6" },
    { id: "item-7", content: "항목 7" },
    { id: "item-8", content: "항목 8" },
    { id: "item-9", content: "항목 9" },
];
export default function Test() {
    const [items, setItems] = useState(initialItems);

    const onDragEnd = (result) => {
        const { source, destination } = result;

        // 드롭 대상이 없는 경우(드롭 영역 밖으로 나갔을 때)
        if (!destination) {
            return;
        }

        // 항목의 위치가 변경되지 않은 경우
        if (
            source.droppableId === destination.droppableId &&
            source.index === destination.index
        ) {
            return;
        }

        // 항목 재배치
        const newItems = Array.from(items);
        const [relocatedItem] = newItems.splice(source.index, 1);
        newItems.splice(destination.index, 0, relocatedItem);

        setItems(newItems);
    };

    return (
        <DragDropContext onDragEnd={onDragEnd}>
            <Droppable droppableId="droppable">
                {(provided) => (
                    <DroppableArea
                        {...provided.droppableProps}
                        ref={provided.innerRef}
                    >
                        {items.map((item, index) => (
                            <Draggable
                                key={item.id}
                                draggableId={item.id}
                                index={index}
                            >
                                {(provided) => (
                                    <View
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        style={{
                                            ...provided.draggableProps.style,
                                        }}
                                    >
                                        {item.content}
                                    </View>
                                )}
                            </Draggable>
                        ))}
                        {provided.placeholder}
                    </DroppableArea>
                )}
            </Droppable>
        </DragDropContext>
    );
}
